/* Heart Animation */
.form-check.wishList input[type="checkbox"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	width: 24px;
	height: 24px;
	margin: 0 12px;
	outline: 12px solid #bdbdbd;
	outline-offset: -12px;
	cursor: pointer;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	border-radius: 50%;
	transform: rotate(45deg) scale(1);
	will-change: transform;
}
.form-check.wishList input[type="checkbox"]:checked {
	border-radius: 50% 50% 0 50%;
	-webkit-animation: heart 1s ease forwards;
	animation: heart 1s ease forwards;
	outline-offset: -12px;
	outline: 12px solid #bdbdbd;
	transform: rotate(45deg) scale(0.65) !important;
}
@-webkit-keyframes heart {
	25% {
		transform: rotate(45deg) scale(1.5);
	}
	75% {
		transform: rotate(45deg) scale(1.25);
	}
	50%,
	100% {
		transform: rotate(45deg) scale(1);
		box-shadow: 0 -12px 0 0 #e44, -12px 0 0 0 #e44;
		outline-color: #e44;
	}
}
@keyframes heart {
	25% {
		transform: rotate(45deg) scale(1.5);
	}
	75% {
		transform: rotate(45deg) scale(1.25);
	}
	50%,
	100% {
		transform: rotate(45deg) scale(1);
		box-shadow: 0 -12px 0 0 #e44, -12px 0 0 0 #e44;
		outline-color: #e44;
	}
}
/* Tabs Animated */
.prod-tabs label {
	--animation-time: 200ms;
	position: relative;
	overflow: hidden;
}
.prod-tabs label::after {
	content: "";
	display: block;
	bottom: 0;
	left: 0;
	width: 100%;
	height: var(--underline-size, 2px);
	position: absolute;
	background-color: var(--underline-color, currentColor);
	transition: transform ease var(--animation-time, 200ms),
		opacity ease 0ms 200ms;
	transform: translateX(100%);
	opacity: 0;
}
.prod-tabs label:hover ~ .prod-tabs label::after {
	transform: translateX(-100%);
}
.prod-tabs:not(:hover) label:not(.active)::after {
	transform: translateY(100%);
}
.prod-tabs .tab-accord:hover label:hover::after,
.prod-tabs:hover .tab-accord:hover label:hover::after {
	transition-delay: 0ms, 0ms;
}
.prod-tabs label.active::after,
.prod-tabs label:hover::after {
	transform: translateX(0%);
	opacity: 1;
}
.prod-tabs {
