}
/* Dynamic tabs */
.tabs.tab-accord {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.tabs.tab-accord label {
	-webkit-box-ordinal-group: 2;
	-webkit-order: 1;
	-ms-flex-order: 1;
	order: 1;
	display: block;
	padding: 1rem 1.5rem 1rem 0;
	margin-right: 1rem;
	cursor: pointer;
	font-weight: bold;
	-webkit-transition: background ease 0.2s;
	transition: background ease 0.2s;
}
.tabs.tab-accord .tab {
	-webkit-box-ordinal-group: 100;
	-webkit-order: 99;
	-ms-flex-order: 99;
	order: 99;
	-webkit-box-flex: 1;
	-webkit-flex-grow: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	width: 100%;
	display: none;
	padding: 2rem 0 0 0;
}
.tabs.tab-accord input[type="radio"] {
	position: absolute;
	opacity: 0;
}
.prod-tabs .tabs.tab-accord input[type="radio"] + label {
	position: relative;
}
.prod-tabs .tabs.tab-accord input[type="radio"]:checked + label::after {
	transform: translateX(0%);
	content: "";
	opacity: 1;
	display: block;
	bottom: 0;
	left: 0;
	width: 100%;
	height: var(--underline-size, 2px);
	position: absolute;
	background-color: var(--underline-color, currentColor);
	transition: transform ease var(--animation-time, 200ms),
		opacity ease 0ms 200ms;
}
.tabs.tab-accord input[type="radio"]:checked + label + .tab {
	display: block;
}
